.about-me {
  display: grid;
  padding: 0 14px 70px;
  background-color: #202020;
  color: white;
}

@media screen and (min-width: 768px) {
  .about-me {
      padding: 0 50px 90px;
      column-gap: 49px;
      align-items: start;
      grid-template-areas:
      'title title'
      'info photo'
      ;
  }
}

@media screen and (min-width: 1280px) {
  .about-me {
      justify-content: space-between;
      padding: 0 70px 100px;
  }  
} 

.about-me__info {
  display: grid;
}

@media screen and (min-width: 768px) {
  .about-me__info {
      grid-template-rows: repeat(3, auto);
      grid-auto-rows: 1fr;
      min-height: 100%;
      align-content: start;
      grid-area: info;
  }
}

@media screen and (min-width: 1280px) {
  .about-me__info {
      max-width: 600px;
  }  
} 

@media screen and (min-width: 768px) {
  .about-me__title {
      grid-area: title;
  }
}

.about-me__photo {
  margin-top: 60px;
  width: 100%;
  object-fit: cover;
  aspect-ratio: 292/352;
  border-radius: 10px;
}

@media screen and (min-width: 768px) {
  .about-me__photo {
      width: 255px;
      margin-top: 66px;
      aspect-ratio: 255/307;
      grid-area: photo;
  }
}

@media screen and (min-width: 1280px) {
  .about-me__photo {
      width: 270px;
      aspect-ratio: 270/327;
  }  
} 

.about-me__name {
  margin: 40px 0 0;
  font-size: 30px;
  font-style: normal;
  font-weight: 400;
}

@media screen and (min-width: 768px) {
  .about-me__name {
      margin: 66px 0 0;
      font-size: 40px;
  }
}

@media screen and (min-width: 1280px) {
  .about-me__name {
      font-size: 50px;
  }
} 

.about-me__job {
  margin: 20px 0 0;
  font-size: 11px;
  font-style: normal;
  font-weight: 500;
  line-height: 20px;
}

@media screen and (min-width: 768px) {
  .about-me__job {
      margin: 16px 0 0;
      font-size: 12px;
  }
}

@media screen and (min-width: 1280px) {
  .about-me__job {
      margin: 18px 0 0;
      font-size: 18px;
  }  
} 

.about-me__description {
  margin: 20px 0 0;
  font-size: 11px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px;
}

@media screen and (min-width: 768px) {
  .about-me__description {
      font-size: 12px;
  }
}

@media screen and (min-width: 1280px) {
  .about-me__description {
      margin: 26px 0 0;
      font-size: 14px;
  }  
} 

.about-me__link {
  padding-top: 40px;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
}

@media screen and (min-width: 768px) {
  .about-me__link {
      padding-top: 10px;
      align-self: end;
  }
}
  