.header {
  display: grid;
  box-sizing: border-box;
  justify-items: center;
  font-family: 'Inter';
  font-weight: 500;
}

.header_type_main {
  background-color: #073042;
}

.header_type_auth {
  padding-top: 5px;
  min-height: 144px;
  grid-template-columns: auto;
  justify-content: center;
}
@media screen and (min-width: 768px) {
  .header_type_auth {
      padding-top: 0;
      margin-top: 232px;
      min-height: 0;
      width: 100%;
  }
}

@media screen and (min-width: 1280px) {
  .header_type_auth {
      margin-top: 70px;
  }
}

.header__container {
  align-items: center;
  box-sizing: border-box;
  display: grid;
  grid-template-columns: auto 1fr;
  min-height: 74px;
  padding: 0 16px;
  position: relative;
  width: 100%;
}

@media screen and (min-width: 768px) {
  .header__container {
      padding: 0 30px;
  }
}

@media screen and (min-width: 1280px) {
  .header__container {
      justify-self: center;
      padding: 0 70px;
      max-width: 1280px
  }
}

@media screen and (min-width: 768px) {
  .header__container_type_auth {
      width: 396px;
      padding: 0;
      min-height: 0;
  }
}
  