.technologies {
  display: grid;
  background-color: #272727;
  color: white;
}

@media screen and (min-width: 1280px) {
  .technologies__container {
      padding-bottom: 100px;
  }
}

@media screen and (min-width: 1280px) {
  .technologies__title {
      padding-top: 100px;
  }
}

.technologies__subtitle {
  margin: 60px 0 0;
  text-align: center;
  font-size: 30px;
  font-style: normal;
  font-weight: 400;
  letter-spacing: -1px;
}

  @media screen and (min-width: 768px) {
      .technologies__subtitle {
          margin: 80px 0 0;
          font-size: 50px;
          letter-spacing: -2px;
      }
  }

  @media screen and (min-width: 1280px) {
      .technologies__subtitle {
          margin: 90px 0 0;
      }
  }

.technologies__description {
  margin-top: 24px;
  text-align: center;
  justify-self: center;
  letter-spacing: -0.45px;
}

  @media screen and (min-width: 768px) {
      .technologies__description {
          margin-top: 22px;
          max-width: 460px;
          letter-spacing: -0.45px;
      }
  }

  @media screen and (min-width: 1280px) {
      .technologies__description {
          margin-top: 26px;
          letter-spacing: -0.55px;
      }
  }

.technologies__list {
  display: grid;
  margin: 0;
  padding: 50px 0 0;
  row-gap: 10px;
  column-gap: 10px;
  justify-content: center;
  grid-template-columns: repeat(2, auto);
  list-style-type: none;
}

  @media screen and (min-width: 768px) {
      .technologies__list {
          padding: 83px 0 0;
          grid-template-columns: repeat(7, auto);
      }
  }

  @media screen and (min-width: 1280px) {
      .technologies__list {
          padding: 100px 0 0;
      }
  }

.technologies__item {
  padding: 21px 10px;
  min-width: 84px;
  text-align: center;
  box-sizing: border-box;
  border-radius: 10px;
  background-color: #303030;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  letter-spacing: -0.5px;
  line-height: 15.5px;
}

@media screen and (min-width: 1280px) {
  .technologies__item {
      min-width: 90px;
      font-size: 14px;
  }
}
