.search-form {
  padding: 60px 10px 0;
  position: relative;
}

@media screen and (min-width: 768px) {
  .search-form {
      padding: 60px 30px 0;
  }
}

@media screen and (min-width: 1280px) {
  .search-form {
      display: flex;
      padding: 80px 70px 94px;
      justify-content: space-between;
  }
}

.search-form::after {
  content: "";
  position: absolute;
  bottom: 10px;
  left: 14px;
  right: 14px;
  height: 1px;
  background-color: rgba(170, 170, 170, 0.2);
  z-index: -2;
}

@media screen and (min-width: 768px) {
  .search-form::after {
      left: 30px;
      right: 30px;
  }
}

@media screen and (min-width: 1280px) {
  .search-form::after {
      left: 70px;
      right: 70px;
  }
}

.search-form__container {
  border: 1.5px solid rgba(170, 170, 170, 0.2);
  border-radius: 50px;
  min-width: 297px;
  height: 48px;
  display: flex;
  flex-direction: row;
  align-items: center;
}


@media screen and (min-width: 1280px) {
  .search-form__container {
      width: 932px;
  }
}

.search-form__input {
  padding: 14.5px;
  border: none;
  background-color: transparent;
  box-sizing: border-box;
  outline: none;
  border-radius: 8px;
  flex: 1;
  font-size: 11px;
  font-style: normal;
  font-weight: 400;
  color: #8B8B8B
}

.search-form__input:focus {
      border: 2px solid rgba(170, 170, 170, 0.2);
      border-right: none;
      border-radius: 50px;
      border-bottom-right-radius: 0;
      border-top-right-radius: 0;
  }

@media screen and (min-width: 768px) {
  .search-form__input {
      font-size: 14px;
      padding: 17.5px;
  }
}

.search-form__button {
  width: 44px;
  height: 44px;
  padding: 0;
  border-radius: 8px;
  background-color: transparent;
  background-image: url('../../images/find.svg');
  background-repeat: no-repeat;
  background-size: 34px;
  background-position: center;
  border: none;
  margin-right: 3.5px;
  margin-bottom: 2.5px;
}

@media screen and (min-width: 767px) {
  .search-form__button {
      margin-right: 0.5px;
      margin-bottom: 1.5px;
  }
}
  